@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;800&family=Oswald:wght@200;300;400;500;600;700&family=Poppins:wght@600&display=swap');

:root {
/*
   ============
    Typography
   ============
*/


/*  colors */
  --btnColor: #3DBA41;
  --black: #000000;
  --white: #ffffff;

  /*  margins */

}

*,
::after,
::before {
  margin: 0;
  border: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  margin-bottom: 20px;
  text-transform: capitalize;
  font-size: 36px;
  font-weight: 800;
}

h2 {
  text-transform: uppercase;
}

h3 {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 400;
}

span {
  font-size: 12px;
  font-weight: 500;
  text-align: center;
}

p {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  font-weight: 500;
}

li {
  list-style-type: none;
}

.btn {
  width: 97px;
  height: 24px;
  border-radius: 50px;
  background-color: var(--btnColor);
  color: var(--white);
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 16px;
}

.header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 30px;
  color: var(--white);
  background-color: var(--black);
  font-family: 'Oswald', sans-serif;
}

.header-title {
  display: flex;
  height: 100%;
}

.header-title--logo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 60px;
}

.header-title--logo img {
  padding-top: 8px;
}

.header-title--text {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.header-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.header-navbar--desktopList {
  display: none;
  justify-content: space-between;
  align-items: center;
  max-width: 520px;
  width: 520px;
  height: 100%;
  margin-inline: 30px;
}

.header-navbar--btn {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 97px;
}

.header-navbar--logo {
  margin: 0 10px;
}

.header-navbar--mobileList {
  position: absolute;
  right: 0;
  top: 60px;
  height: 80vh;
  width: 300px;
  background-color: var(--black);
  transition: display 1s ease-in, right 0.3s ease-in;
}

.header-navbar--mobileList li {
  margin: 20px;
}

.mobileList-active {
  right: 0;
  top: 60px;
}

.mobileList-passive {
  right: -305px;
  top: 60px;
}

.content {
  display: grid;
  grid-template-columns: 100%;
  font-family: 'Montserrat', sans-serif;
}

.content-title {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 60% 40%;
  justify-content: center;
  height: 100px;
  margin-top: 120px;
  margin-bottom: 50px;
}

.content-title--largeText {
  display: flex;
  justify-content: center;
  max-width: 980px;
  margin: auto;
}

.content-title--smallText {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 980px;
  margin: auto;
}

.content-body {
  position: relative;
  display: grid;
  grid-template-rows: 50% 50%;
  width: 100%;
}

.content-body--left,
.content-body--right {
  position: absolute;
  width: 100%;
  height: 1280px;
}

.content-body--left {
  top: 0;
  left: 0;
  z-index: 999;
  height: 1280px;
  background-image: url("resource/bG-1.svg");
}

#canine {
  position: absolute;
  top: -50px;
  left: calc(50vw - 155px);
  width: 330px;
}

.content-body--right {
  top: 1100px;
  left: 0;
  z-index: 999;
  height: 980px;
  background-image: url("resource/bG-2.svg");
}

#feline {
  position: absolute;
  top: 100px;
  left: calc(50vw - 155px);
  width: 330px;
}
#comingSoon {
  width: 330px;
}

.content-body--backGround {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
}

.content-body--backGround img {
  width: 100%;
  height: 372px;
}

.content-button--box {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 300px;
  color: var(--white);
}

.content-btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 159px;
  height: 47px;
  margin: 15px;
  background: linear-gradient(180deg, #3DBA41 0%, #007B04 100%);
}

.content-btn--text {
  font-family: 'Poppins', sans-serif;
  font-size: 13px;
  font-weight: 600;
}

.content-btn--logo {
  width: 40px;
  height: 40px;
}

.content-btn--logo {
  height: 17px;

}

.content-token {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-top: 50px;
}

.content-token--title {
  width: 80%;
  margin-bottom: 30px;
  color: var(--white);
  text-align: center;
}

.content-token--tokens {
  display: flex;
  justify-content: center;
  width: 80%;
}

#tokens {
  width: 310px;
  height: 223px;
}

.content-tax {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin: 30px;
  height: 200px;
}

.content-tax--title {
  text-align: center;
  color: var(--white);
}

.content-tax--taxes {
  display: flex;
  justify-content: space-between;
  width: 350px;
  height: 100px;
}

.taxes-line {
  height: 100%;
  width: 1px;
  background-color: var(--white);
}

.taxes--title {
  color: var(--btnColor);
  font-family: 'Poppins', sans-serif;
  font-size: 19px;
  font-weight: 700;
}

.taxes--text {
  color: var(--white);
  font-family: 'Poppins', sans-serif;
  font-size: 19px;
  font-weight: 700;
}

.content-comingSoon {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 500px;
}

.content-para {
  width: 100%;
  margin-top: 100px;
  text-align: center;
}

#buy,
#chart,
#contract {
  width: 17px;
  height: 16px;
  margin-right: 5px;
}

#rect-1,
#rect-2 {
  position: absolute;
}

#rect-1 {
  left:-14px;
}

#rect-2 {
  right:-14px;
}

@media all and (min-width: 980px){

  h1 {
    font-size: 64px;
  }

  .header-navbar--desktopList {
    display: flex;
  }

  .header-navbar--mobileList {
    display: none;
  }

  .header-navbar--logo {
    display: none;
  }

  .content-title {
    height: 150px;
    margin-top: 150px;
    margin-bottom: 80px;
  }

  .content-title--largeText,
  .content-title--smallText {
    width: 980px;
